import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
// import { BeatLoader } from "react-spinners";
import "./FormMap.css";
import {
  checkForAddressExistance,
  clearGivenRange,
  getAddressWithOutSheetName,
} from "../../../constFunctions/excelFunction";
import DropDown from "../../DropDown/DropDown";
import Button from "../../Button/Button";
import { checkAlreadyReservedRange, logout, showErrorMessage } from "../../../constFunctions/copmponentFunctions";
import { setFormMappingData, setNextPage } from "../../../redux-toolkit/slice/TaskpaneSlice";
import { setFormMappingData2 } from "../../../redux-toolkit/slice/TaskpaneSlice";
import CloseLogo from "../../../../../assets/icons/x-circle.svg";
import EditLogo from "../../../../../assets/icons/editLogo.svg";
import { runQuery } from "../../../api/apiCalls";
import { DTX_GRAPHQL_URL } from "../../../../config";
import { JWT_ERROR_MESSAGE } from "../../../constFunctions/constants";
import {
  GetAllFormTemplatesQuery,
  GetDetailFormTemplateQuery,
} from "../../../api/graphQlQuery";

function formMap() {
  const { formMappingData } = useSelector((state) => state.taskpane);
  const [rangeFor, setRangeFor] = useState({});
  const [edit, setEdit] = useState(false);
  const [allFromTemplates, setallFromTemplates] = useState([]);
  const [selectedForm, setSelectedForm] = useState([]);
  const [allFormFields, setallFormFields] = useState([]);
  const [formLoader, setFormLoader] = useState();
  const [fieldLoader, setFieldLoader] = useState();
  const dispatch = useDispatch();

  useEffect(() => {
    setFormLoader(true);
    try {
      const body = {
        operationName: "GetAllFormTemplatesQuery",
        query: GetAllFormTemplatesQuery,
        variables: {},
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body).then(async (data) => {
        if (data?.errors) {
          data.errors.map((element) => {
            if (element?.message.includes(JWT_ERROR_MESSAGE)) {
              logout();
              dispatch(setNextPage("homeLogin"));
            }
          });
        } else {
          let formTypes = data?.data?.core_form_templates?.map((element) => {
            return { label: element.name, value: element.id };
          });
          setallFromTemplates(formTypes);
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      setFormLoader(false);
    }
  }, [true]);
  useEffect(() => {
    setRangeFor(null);
    setFieldLoader(true);
    try {
      const body = {
        operationName: "GetDetailFormTemplateQuery",
        query: GetDetailFormTemplateQuery,
        variables: {
          id: selectedForm?.value,
        },
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body).then(async (data) => {
        if (data?.errors) {
          data.errors.map((element) => {
            if (element?.message.includes(JWT_ERROR_MESSAGE)) {
              logout();
              dispatch(setNextPage("homeLogin"));
            }
          });
        } else {
          let fields = data?.data?.core_form_templates_by_pk?.template_versions[0].template_fields?.map((element) => {
            return { label: element.caption, value: element.field_name };
          });
          setallFormFields(fields);
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      setFieldLoader(false);
    }
  }, [selectedForm]);
  const handleFieldChanges = (rangeFor) => {
    setRangeFor(rangeFor);
  };
  const handleFormSelectionChange = (selectedForm) => {
    setSelectedForm(selectedForm);
  };
  //function  reserveSelectedSpace  which stores selected address into MMmapping data (state in redux)
  // you have to select what kind of region you are looking for ,if we want select entire colummn then need to select "column" from range type
  // if first column of the excel has selected the the address by default  "A:A" or "A1:Ax" or (if you selected a region like "A10:C20") will be changed to "A"
  //mappingConfig is an object will store the selected address
  // mappingConfig={
  // custom_material_id:{ name: "Custom Material Id* ", address: "A" }
  //}
  const reserveSelectedSpace = async () => {
    try {
      rangeFor?.label &&
        (await Excel.run(async (context) => {
          const range = context.workbook.getSelectedRange();
          range.load("address");
          await context.sync();
          let selectedAddress = getAddressWithOutSheetName(range.address);
          //checkAlreadyReserved is for checking whether the selelcted colum is reserved or
          checkAlreadyReservedRange(formMappingData, selectedAddress).then(async (addressCheck) => {
            if (!addressCheck) {
              const range = context.workbook.worksheets.getActiveWorksheet().getRange(String(selectedAddress));
              range.merge();
              await context.sync();

              dispatch(
                setFormMappingData({
                  name: rangeFor.label,
                  value: rangeFor.value,
                  address: selectedAddress,
                  addressAfterMerge: selectedAddress.split(":")[0],
                })
              );

              let selectedRange = context.workbook.worksheets
                .getActiveWorksheet()
                .getRange(String(selectedAddress.split(":")[0]));
              selectedRange.format.fill.color = "#FFF0D9";
              selectedRange.values = [[rangeFor.label]];
              // selectedRange.format.autofitRows();
              // selectedRange.format.autofitColumns();
              return context.sync();
            } else {
              showErrorMessage(
                "reserved-column-error",
                ` Range ${selectedAddress}  is a reserved space`,
                "apply-button"
              );
            }
          });
        }));
    } catch (error) {
      console.log("Error:", error);
    }
  };

  const saveMMmappingConfig = () => {};
  // deleteCurrentRow == > deletes the selected address from mapping data
  const deleteCurrentRow = (address, key) => {
    clearGivenRange(address);
    let formMappingDataCopy = { ...formMappingData };
    let array = formMappingData[key].address.filter((element) => element !== address);
    console.log(array);
    formMappingDataCopy = {
      ...formMappingData,
      [key]: {
        ...formMappingData[key],
        address: array,
      },
    };
    if (formMappingDataCopy[key]?.address.length < 1) {
      delete formMappingDataCopy[key];
    }
    if (Object.keys(formMappingDataCopy).length) {
      dispatch(setFormMappingData2(formMappingDataCopy));
    } else {
      dispatch(setFormMappingData2({}));
    }
  };
  return (
    <div className="material-map-box">
      <div id="app-div" className="app-div">
        <div className="select-map-feild">
          <DropDown
            options={allFromTemplates}
            handleChange={handleFormSelectionChange}
            value={selectedForm}
            label={"Selelct the form Template"}
            noOptionMessage={"No mapping fields availble"}
            isLoading={formLoader}
          />
          <DropDown
            options={allFormFields}
            handleChange={handleFieldChanges}
            value={rangeFor}
            label={"Selelct the field"}
            noOptionMessage={"No mapping fields availble"}
            isLoading={fieldLoader}
          />
          <div className="action">
            <p className="warning" id="warning"></p>
          </div>
          <div className="action">
            {
              <span id="apply-button" onClick={() => selectedForm && rangeFor && reserveSelectedSpace()}>
                <Button label="Apply" enable={selectedForm && rangeFor ? true : false} />
              </span>
            }
          </div>
          <blockquote className="reserved-column-error" id="reserved-column-error"></blockquote>

          <table id="configure-table">
            <thead>
              <tr>
                <th>Mapping to </th>
                <th>Address </th>
                <th onClick={() => setEdit(!edit)}>
                  <img className="sidebar-logo-img" src={EditLogo} />
                </th>
              </tr>
            </thead>
            <tbody>
              {formMappingData &&
                Object.entries(formMappingData)?.map(
                  ([key, value]) =>
                    value?.address && (
                      <tr key={key}>
                        <td>{value.name}</td>
                        <td className="address-cell">
                          {" "}
                          {value?.address.map((address, index) => (
                            <span className="address-and-close">
                              {" "}
                              <p className="table-address" key={index}>
                                {address}
                              </p>
                              {edit && (
                                <span
                                  onClick={() => {
                                    deleteCurrentRow(address, key);
                                  }}
                                >
                                  <img className="sidebar-logo-img" src={CloseLogo} key={index} />
                                </span>
                              )}
                            </span>
                          ))}{" "}
                        </td>
                      </tr>
                    )
                )}
            </tbody>
          </table>
          <div className="upload">
            <button
              onClick={() => {
                saveMMmappingConfig();
              }}
            >
              Save
            </button>
          </div>
        </div>
      </div>

    </div>
  );
}

export default formMap;
