import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { setFormAction } from "../../../redux-toolkit/slice/action";
import "./FormNavbar.css";
import { BASE_URL } from "../../../../config";

function FormNavbar() {
  const { nextPage } = useSelector((state) => state?.taskpane);
  const dispatch = useDispatch();
  const { formAction } = useSelector((state) => state?.action);
  return (
    <div className="navbar">
      <div className="navbar-logo">
        <img src={`${BASE_URL}/assets/beacon-without-background -logo.png `} />
      </div>
      {nextPage === "forms" && (
        <div className="navbar-option">
          <span
            className={`navbar-button ${formAction == "get" ? "selectedAction" : null}`}
            onClick={() => dispatch(setFormAction("get"))}
          >
            Get
          </span>
          <span
            className={`navbar-button ${formAction == "map" ? "selectedAction" : null}`}
            onClick={() => dispatch(setFormAction("map"))}
          >
            Map
          </span>
          <span
            className={`navbar-button ${formAction == "export" ? "selectedAction" : null}`}
            onClick={() => dispatch(setFormAction("export"))}
          >
            Export
          </span>
          <span
            className={`navbar-button ${formAction == "update" ? "selectedAction" : null}`}
            onClick={() => dispatch(setFormAction("update"))}
          >
            Update
          </span>
        </div>
      )}
    </div>
  );
}

export default FormNavbar;
