import React, { Suspense, useEffect } from "react";
import SideBarMenu from "../../commonComponents/SideBarMenu/SideBarMenu";
import "./Forms.css";
import FormNavbar from "../../commonComponents/Form/FormNavBar/FormNavbar";
import FormMap from "../../commonComponents/Form/Formmap/FormMap";
import MaterialGet from "../../commonComponents/Material/MaterialGet/MaterialGet";
import MaterialExport from "../../commonComponents/Material/MaterialExport/MaterialExport";
import MaterialMap from "../../commonComponents/Material/MaterialMap/MaterialMap";
import { useSelector } from "react-redux";

function Forms() {
  const { formAction } = useSelector((state) => state.action);
  return (
    <>
      <div className="commonContainer">
        <div className="commonNavBar">
          <FormNavbar />
        </div>
        <div className="commonBody ">
          <div className="commonSidebarBox">
            <SideBarMenu />
          </div>
          <div className="commonActivityArea">
            {" "}
            {formAction === "get" ? (
              <Suspense fallback={<div></div>}>
                <MaterialGet />{" "}
              </Suspense>
            ) : null}
            {formAction === "map" ? (
              // <Suspense fallback={<div></div>}>
              <FormMap
              // mappingConfig={MMmappingData}
              // setmappingConfig={setMMmappingTempData}
              // setmappingConfig2={setMMmappingTempData2}
              />
            ) : // </Suspense>
            null}
            {formAction === "export" ? (
              <Suspense fallback={<div></div>}>
                {" "}
                <MaterialExport />{" "}
              </Suspense>
            ) : null}
            {formAction === "update" ? (
              <Suspense fallback={<div></div>}>
                {" "}
                <MaterialGet />{" "}
              </Suspense>
            ) : null}
          </div>
        </div>
      </div>
    </>
  );
}

export default Forms;
